import * as React from 'react';
import classes from "./RectangularWaveguides.module.sass";
import {Container} from "react-bootstrap";
import expImg from "../img/exponential_growth_520.jpg";
import {Slider, TextField} from "@mui/material";
import {useEffect, useState} from "react";
import clsx from "classnames";


export function RectangularWaveguides() {

    const [translateX, setTranslateX] = useState(0) //  
    const [sliderValue, setSliderValue] = useState(0) //  slider

    useEffect(() => {

        setTranslateX(sliderValue * 3);
    }, [sliderValue])


    return (
        <>
            <h1>    </h1>
            <div className={classes.flexContainer}>
                <div className={classes.flexContainerItem}>
                    <TextField
                        label=" ()"
                        type="number"
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </div>


                <div className={classes.flexContainerItem}>
                    <TextField
                        label=""
                        defaultValue="1 "
                        InputProps={{
                            readOnly: true,
                        }}
                    />
                </div>
            </div>
            <h2> :</h2>
            <div className={clsx(classes.flexContainer, classes.flexItemSlider)}>

                <div className={classes.imgBox}>
                    <img src={expImg} alt={'img'}/>
                    <div className={(classes.verticalLine)} style={{transform: `translateX(${translateX}px`}}></div>
                </div>

                <div className={classes.sliderBox}>
                    <Slider
                        value={sliderValue}
                        onChange={(e) => {
                            setSliderValue(e.target.value)
                        }}
                        min={0}
                        max={100}
                    />
                </div>
            </div>

        </>

    );
}